@use "global" as *;

.p-single-body__inner.l-inner {
  max-width: rem(800);
}

.p-single-body__title h1 {
  font-size: rem(22);
  line-height: 1.5455;
  font-weight: $normal;
  @include mq(md) {
    font-size: rem(32);
    line-height: 1.4375;
  }
}

.p-single-body__meta {
  margin-top: rem(48);
  @include mq(md) {
    margin-top: rem(10);
  }
}

.p-single-body__image {
  margin-top: rem(14);
  position: relative;
  @include mq(md) {
    margin-top: rem(27);
  }
}

.p-single-body__image::before {
  content: "";
  display: block;
  padding-top: 55.8%;
}

.p-single-body__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

// ↓ セレクターに対して当てていってOK。ここに関しては入れ子を使った方が分かりやすい
.p-single-body__content {

  p {
    font-size: rem(14);
    line-height: 1.7143;
    margin-top: rem(30);
  }

  h2 {
    margin-top: rem(56);
    padding-left: rem(17);
    font-size: rem(18);
    line-height: 1.167;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      left: rem(-3);
      width: rem(6);
      height: 100%;
      background-color: #391E88;
      border-radius: 5px;  // カンプで数値が出ないので目視でやった
    }
    @include mq(md) {
      margin-top: rem(82);
      font-size: rem(24);
    }
  }

  h3 {
    margin-top: rem(56);
    font-size: rem(16);
    line-height: 1.167;
    @include mq(md) {
      font-size: rem(18);
      line-height: 1.5;
    }
  }
}

